        .MainWindow .LeftPanel .OperationsPanel .icon.working.manual-wenting svg {
          -webkit-animation: rotation 1s infinite linear; }

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(359deg); } }

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(359deg); } }
        .MainWindow .LeftPanel .OperationsPanel .icon.working.power-generation svg {
